.header {
  background-color: #000;
  padding: rem(10);
  .wrapper {
    display: flex;
    flex-direction: row;
    align-items: center;
  }
  .logo-wrapper {
    flex: 0 0 11%;
  }
  .menu-wrapper,
  .login-wrapper {
    display: none;
  }
  .search-wrapper {
    display: flex;
    flex-grow: 1;
    justify-content: flex-end;
    align-items: center;
    padding: 0 rem(45);
    position: relative;
    .search__form {
      display: none;
      position: absolute;
      padding: rem(15);
      background-color: #000;
      width: rem(320);
      .search__btn {
        padding: 0;
        background-color: #000;
        border: 0;
        margin-right: rem(35);
        &:hover {
          opacity: 0.7;
          transition: 0.3s;
        }
      }
      .search {
        border: none;
        background-color: #000;
        color: #999;
        flex-grow: 1;
      }
    }
    label,
    img,
    .search__btn {
      width: 24px;
      height: 24px;
      cursor: pointer;
    }
    input[type="checkbox"] {
      display: none;
      &:checked ~ input[type="search"] {
        display: block;
        margin-right: rem(10);
      }
      &:checked ~ label {
        display: none;
      }
      &:checked ~ .search__form {
        display: flex;
      }
    }
  }
  .mobile-menu-wrapper {
    display: block;
    flex: 0 0 6%;
    position: relative;
    label,
    img {
      width: 30px;
      height: 24px;
      cursor: pointer;
    }
    .menu-mobile {
      position: absolute;
      display: none;
      flex-direction: column;
      z-index: 1;
      border-radius: 2px;
      background-color: #fafafa;
      right: 0px;
    }
    input[type="checkbox"] {
      display: none;
      &:checked + .menu-mobile {
        display: flex;
      }
    }
  }
}
.has-sub {
  position: relative;
  border-top: 4px solid transparent;
}
.has-sub:hover .sub-menu__wrapper {
  display: block;
}
.sub-menu {
  &__wrapper {
    padding-right: rem(10);
    position: absolute;
    display: none;
    top: 0px;
    right: 114px;
    z-index: 1;
  }
  display: flex;
  flex-direction: column;
  border-radius: 2px;
  background-color: #fafafa;
}
.menu-mobile__item {
  display: block;
  padding: rem(18) rem(20);
  font-size: rem(14);
  color: #999;
  text-transform: uppercase;
  white-space: nowrap;
  &:hover {
    color: #2196f3;
    background-color: #eeeeee;
    transition: 0.3s;
  }
}

@media (min-width: 768px) {
  .header {
    padding: 0;
    .wrapper {
      padding: rem(20) rem(44) 0 rem(44);
      justify-content: space-between;
    }
    .logo-wrapper {
      align-self: flex-start;
    }
    .mobile-menu-wrapper {
      display: none;
    }
    .menu-wrapper {
      display: flex;
      flex-grow: 1;
      align-items: center;
      .menu__item {
        &:first-child {
          display: none;
        }
        text-transform: uppercase;
        padding: rem(27) rem(14) rem(22) rem(14);
        border-bottom: 4px solid transparent;
        margin-right: rem(49);
        &:hover {
          color: #fff;
          border-bottom: 4px solid #fff;
          transition: color 0.3s;
          transition: border-bottom 0.3s;
        }
        &:last-child {
          margin-right: 0;
        }
        &--active {
          color: #fff;
          border-bottom: 4px solid #2196f3;
        }
      }
    }
    .login-wrapper {
      display: block;
      padding-right: rem(30);
      .login {
        color: #fff;
        text-transform: capitalize;
        font-size: rem(14);
        &:hover {
          color: rgba($color: #fff, $alpha: 0.7);
          transition: 0.3s;
        }
      }
    }
    .search-wrapper {
      padding: 0;
      flex-grow: 0;
      input[type="checkbox"]:checked ~ input[type="search"] {
        position: absolute;
        width: 25%;
        right: 355px;
        top: 40px;
      }
      label {
        &:hover {
          opacity: 0.7;
          transition: 0.3s;
        }
      }
      label,
      img,
      .search__btn {
        width: 18px;
        height: 18px;
      }
    }
    .sub-menu__wrapper {
      top: 65px;
      right: 44px;
    }
    .has-sub {
      .menu__item {
        &:first-child {
          display: flex;
        }
        display: flex;
        flex-direction: row;
        padding: rem(22) rem(30);
        &::after {
          content: url("../img/icon-arrow-up.png");
          display: none;
          margin-left: rem(23);
        }
      }
      &:hover .menu__item {
        background-color: #fafafa;
        color: #999;
        transition: 0.3s;
        border-radius: 2px;
      }
      &:hover .menu__item::after {
        display: block;
      }
      &:hover + .menu__item {
        margin-left: -34px;
      }
    }
  }
}
@media (min-width: 950px) {
  .header .menu-wrapper .menu__item:first-child {
    display: flex;
  }
}
