html {
  font-family: "Roboto", sans-serif;
  font-weight: 400;
}
.container {
  max-width: 100%;
}
.wrapper {
  max-width: 500px;
  margin: 0 auto;
  padding: rem(8);
}
.title {
  font-family: Helvetica, sans-serif;
  font-size: rem(18);
  color: #000;
  font-weight: 500;
}
.text {
  font-size: rem(16);
  color: #4d4d4d;
  font-weight: 100;
}
body {
  display: flex;
  flex-direction: column;
}
header {
  order: 1;
}
main {
  order: 2;
}
footer {
  order: 3;
}
@media (min-width: 501px) {
  .wrapper {
    max-width: 768px;
  }
}
@media (min-width: 768px) {
  .wrapper {
    padding: rem(44);
    max-width: 1180px;
  }
}
