@import "helpers/variables";
@import "helpers/mixins";
@import "helpers/functions";
@import "vendor/libs";
@import "base/fonts";
@import "base/general";
@import "../blocks/modules";

// VIDEO PAGE
.main {
  background-color: #f3f3f3;
  .wrapper {
    display: flex;
    flex-flow: row wrap;
    justify-content: center;
  }
  .block {
    display: flex;
    flex-flow: column nowrap;
    padding-bottom: rem(7);
    white-space: nowrap;
    overflow: hidden;
    .media {
      flex: 0 1 73%;
      img {
        width: 100%;
      }
    }
    .content {
      flex: 0 0 27%;
      padding: rem(30);
      background-color: #fff;
      .title {
        margin-bottom: rem(5);
        overflow: hidden;
        text-overflow: ellipsis;
      }
    }
  }
  .block--big {
    padding-bottom: rem(17);
    white-space: normal;
    overflow: visible;
    .media {
      position: relative;
      .btn {
        position: absolute;
        width: 72px;
        height: 72px;
        border-radius: 50%;
        border: none;
        background-color: #2196f3;
        box-shadow: 0px 1px 4px 0px rgba(0, 0, 0, 0.4);
        top: 90%;
        left: 80%;
        cursor: pointer;
        &:hover {
          background-color: #067ad7;
          transition: 0.3s;
        }
        img {
          height: 21px;
          width: 18px;
        }
      }
    }
    .content {
      overflow: visible;
      .title {
        font-size: rem(36);
        margin-bottom: rem(25);
      }
      .text {
        font-size: rem(18);
      }
    }
  }
}
// END VIDEO PAGE
// ARTTICLE PAGE
.article {
  .profile {
    background-color: #fff;
    padding: rem(30) rem(30) rem(15) rem(30);
    display: flex;
    flex-direction: row;
    .image {
      border-radius: 50%;
      width: 40px;
      height: 40px;
      margin-right: rem(15);
    }
    .heading {
      .text {
        color: #4d4d4d;
        margin-bottom: rem(7);
      }
      .caption {
        font-size: rem(14);
        color: #999;
      }
    }
  }
  .content {
    .title {
      font-size: rem(36);
      font-weight: 600;
    }
  }
  .btn {
    display: inline-block;
    background-color: #2196f3;
    border-radius: 2px;
    box-shadow: 0px 1px 4px 0px rgba(0, 0, 0, 0.5);
    text-transform: uppercase;
    padding: rem(15) rem(40);
    margin: rem(35) 0 rem(20) 0;
    color: #fff;
    &:hover {
      background-color: #067ad7;
      transition: background-color 0.3s;
    }
  }
  .navigation {
    background-color: #2196f3;
    .wrapper {
      padding: rem(50) rem(40) rem(30) rem(40);
      display: flex;
      justify-content: space-between;
      .navigation__btn {
        display: flex;
      }
      .text {
        color: #fff;
        font-size: rem(19);
        font-weight: 600;
      }
      .previous::before {
        content: url("../img/article/left_arrow.png");
        display: block;
        width: 16px;
        height: 16px;
        margin-right: rem(14);
      }
      .next::after {
        content: url("../img/article/right_arrow.png");
        display: block;
        width: 16px;
        height: 16px;
        margin-left: rem(14);
      }
    }
  }
}
// END ARTICLE PAGE
// VIDEO PAGE
@media (min-width: 501px) {
  .main {
    .wrapper {
      padding: rem(36);
      .block--big {
        flex-basis: 100%;
        .title {
          font-size: rem(36);
        }
        .text {
          font-size: rem(18);
        }
        .media {
          .btn {
            left: 85%;
          }
        }
      }
    }
    .block {
      flex-basis: 50%;
      padding: rem(8);
    }
  }
}
// END VIDEO PAGE
// ARTTICLE PAGE
// END ARTICLE PAGE
// VIDEO PAGE
@media (min-width: 769px) {
  .main {
    .block--big {
      .media {
        .btn {
          left: 90%;
        }
      }
      .title {
        max-width: 70%;
      }
    }
    .block {
      flex-basis: 33.333%;
    }
  }
}
// END VIDEO PAGE
// ARTTICLE PAGE
// END ARTICLE PAGE
