.footer {
  .wrapper {
    padding: rem(21) 0;
  }
  .text {
    font-size: rem(14);
    color: #999;
    text-align: center;
  }
  .menu {
    display: none;
  }
}
@media (min-width: 501px) {
  .footer {
    .wrapper {
      padding: rem(40) rem(44);
      display: flex;
      flex-direction: row;
      justify-content: space-between;
    }
    .menu {
      display: flex;
      flex-flow: row wrap;
      .text {
        text-transform: capitalize;
        margin-right: rem(30);
        &:hover {
          color: #000;
        }
      }
    }
    .text {
      text-align: right;
    }
  }
}
